* {
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
 }

 /*background picture and body*/
html, body {
   height: 100%;
   width: 100%;
}

.navbar {
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: space-evenly;
           justify-content: space-evenly;
   -webkit-align-items: center;
           align-items: center;
   height: 75px;
   background-color: #343a40
}

.home, .projects, .contact {
   padding-top: 10px;
}

.nav-links {
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: flex-end;
           justify-content: flex-end;
   font-weight: 300;
   list-style: none;
   font-size: 24px;
   position: absolute;
   right: 30px;
   bottom: -6px;
}

.mobile-menu-icon {
   display: none;
}

/* Media query for smaller screens */
@media screen and (max-width: 633px) {
   h1 {
      display: -webkit-flex;
      display: flex;
      position: absolute;
      top: 15px;
      left: 35px;
   }

   .nav-links {
      display: none;
   }

   .nav-links-mobile {
      position:absolute;
      display: block;
      list-style: none;
      background-color: rgb(37, 53, 52);
      left: 0;
      top: 75px;
      width: 100%;
      transition: all 0.5s ease-out;
      text-align: center;
      font-size: 30px;
      z-index: 1;
   }

   .home,.projects,.contact {
      color: rgb(245, 245, 245);
      text-align: center;
      padding: 32px;
      transition: all 0.5s ease;
      width: 100%;
   }

   button.mobile-menu-icon {
      display: block;
      position: absolute;
      font-size: 30px;
      top: 15px;
      right: 25px;
      outline: none;
      border: none;
      padding: 0 10px 0 10px;
      border-radius: 10%;
   }
}

/* for bigger screens.
Had to mix up .nav-links-moblie with nav links code*/
@media screen and (min-width: 634px) {
    h1 {
      display: -webkit-flex;
      display: flex;
      position: absolute;
      top: 15px;
      left: 35px;
   }
   
   .mobile-menu-icon {
      display: none;
   }

   /*had to change the properties for menu to stay put*/
   .nav-links-mobile {
